import React, { Component } from 'react';
import './Calculator.css';

class Calculator extends Component {
    constructor(props){
        super(props)
        this.state = {
            visible: false,
            rate: 0,
            expenses: 0,
            totalFees: 0,
            incomeTaxFixed: 0,
            nationalInsuranceFixed: 0,
            combinedFixed: 0,
            insideAnnualFixed: 0,
            insideMonthlyFixed: 0,
            earnings: 0,
            corporationTaxFixed: 0,
            incomeTaxOutsideFixed: 0,
            combinedOutsideFixed: 0,
            outsideAnnualFixed: 0,
            outsideMonthlyFixed: 0
        }
    }
    
    handleInputChange = (event) => {
        event.preventDefault()
        this.setState({
            [event.target.name]: parseInt(event.target.value, 10)
        }, () => {
            this.setState({
                totalFees: this.state.rate * 220,
                earnings: (this.state.rate * 220) - this.state.expenses
            })
        })
    }

    handleSubmit = (event) => {
        event.preventDefault()
        
        // Inside IR35 calculation

        let incomeTax = 0;
        let nationalInsurance = 0;
        
        if (this.state.totalFees <= 8632) {
            incomeTax = 0;
            nationalInsurance = 0;
        } else if (this.state.totalFees <= 12500) {
            incomeTax = 0;
            nationalInsurance = ((this.state.totalFees - 8632) * .12);
        } else if (this.state.totalFees <= 50000) {
            incomeTax = ((this.state.totalFees - 12500) * .2);
            nationalInsurance = ((this.state.totalFees - 8632) * .12);
        } else if (this.state.totalFees <= 150000) {
            incomeTax = (7500 + ((this.state.totalFees - 50000) * .4));
            nationalInsurance = (4964.16 + ((this.state.totalFees - 50000) * .02));
        } else {
            incomeTax = (47500 + ((this.state.totalFees - 150000) * .45));
            nationalInsurance = (4964.16 + ((this.state.totalFees - 50000) * .02));
        }

        const combined = incomeTax + nationalInsurance;
        const insideAnnual = this.state.totalFees - combined;
        const insideMonthly = Math.round((insideAnnual / 12) * 100) / 100;

        const incomeTaxFixed = incomeTax.toFixed(2);
        const nationalInsuranceFixed = nationalInsurance.toFixed(2);

        const combinedFixed = combined.toFixed(2);
        const insideAnnualFixed = insideAnnual.toFixed(2);
        const insideMonthlyFixed = insideMonthly.toFixed(2);

        // Outside IR35 calculation

        const earnings = this.state.totalFees - this.state.expenses;

        let salary = 0;
        let corporationTax = 0;
        let netProfit = 0;

        let dividends = 0;
        let incomeTaxOutside = 0;
        let additional = 0;
        let paLost = 0;
        let over100 = 0;

        if (earnings <= 8500) {
            salary = earnings;
            corporationTax = 0;
        } else {
            salary = 8500;
            corporationTax = (earnings - 8500) * .19;
            netProfit = (earnings - 8500) - corporationTax;
        }

        if (netProfit <= 6000) {
            // 6000 accounts for 4000 remaining PA and 2000 DA combined
            dividends = netProfit;
        } else if (netProfit <= 41500) {
            incomeTaxOutside = (netProfit - 6000) * .075;
            dividends = 6000 + ((netProfit - 6000) * .925);
        } else if (netProfit <= 141500) {
            incomeTaxOutside = ((netProfit - 41500) * .325) + 2662.50;
            dividends = ((netProfit - 41500) * .675) + 32837.50 + 6000;
            //incomeTaxOutside = parseInt(((netProfit - 41500) * .325) + 2662.50, 10);
            //dividends = parseInt(((netProfit - 41500) * .675) + 32837.50 + 6000, 10);
        } else {
            incomeTaxOutside = ((netProfit - 141500) * .381) + 35162.50;
            dividends = ((netProfit - 141500) * 619) + 64562.50 + 6000;
            //additional = netProfit - 141500;
        }

        // £100,000 threshold composed of £91,500 profits and £8,500 salary
        over100 = netProfit - 91500;
        if (over100 > 0 && over100 >= 25000) {
            // If threshold is met, paLost accounts for stealth tax
            // £1 of personal allowance lost for each £2 earned over £100,000 up to £125,000
            paLost = 25000 * .1625;
            incomeTaxOutside += paLost;
        } else if (over100 > 0 && over100 < 25000) {
            paLost = over100 * .1625;
            incomeTaxOutside += paLost;
        }

        const combinedOutside = corporationTax + incomeTaxOutside;
        const outsideAnnual = (salary + dividends) - paLost;
        const outsideMonthly = Math.round((outsideAnnual / 12) * 100) / 100;
        
        const combinedOutsideFixed = combinedOutside.toFixed(2);
        const outsideAnnualFixed = outsideAnnual.toFixed(2);
        const outsideMonthlyFixed = outsideMonthly.toFixed(2);
        
        const incomeTaxOutsideFixed = incomeTaxOutside.toFixed(2);
        const corporationTaxFixed = corporationTax.toFixed(2);

        this.setState({
            // Inside IR35 variables
            incomeTaxFixed: incomeTaxFixed,
            nationalInsuranceFixed: nationalInsuranceFixed,
            combinedFixed: combinedFixed,
            insideAnnualFixed: insideAnnualFixed,
            insideMonthlyFixed: insideMonthlyFixed,
            // Outside IR35 variables
            earnings: earnings,
            salary: salary,
            corporationTaxFixed: corporationTaxFixed,
            netProfit: netProfit,
            dividends: dividends,
            incomeTaxOutsideFixed: incomeTaxOutsideFixed,
            additional: additional,
            outsideAnnualFixed: outsideAnnualFixed,
            outsideMonthlyFixed: outsideMonthlyFixed,
            combinedOutsideFixed: combinedOutsideFixed,
            paLost: paLost
        });
    }
    
    render() {
        const buttonText = this.state.combined ? "Update Calculation" : "Calculate Tax";
        // Inside IR35 variables
        const {rate} = this.state
        const {totalFees} = this.state
        const {incomeTaxFixed} = this.state
        const {nationalInsuranceFixed} = this.state
        const {combinedFixed} = this.state
        const {insideAnnualFixed} = this.state
        const {insideMonthlyFixed} = this.state
        // Outside IR35 variables
        const {expenses} = this.state
        const {earnings} = this.state
        const {corporationTaxFixed} = this.state
        const {outsideAnnualFixed} = this.state
        const {outsideMonthlyFixed} = this.state
        const {incomeTaxOutsideFixed} = this.state
        const {combinedOutsideFixed} = this.state
        
        return (
            <div className="calculator">
              <div className="calc-div" id="left-top">
                <form onSubmit={this.handleSubmit}>
                    <p>My daily contract rate is £ <input type="text" name="rate" onChange={this.handleInputChange}></input></p>
                    <p>My annual expenses amount to £ <input type="text" name="expenses" onChange={this.handleInputChange}></input></p>
                    <button type="submit">{buttonText}</button>
                </form>
                <p>The following calculations are based on your daily contract rate of <p className={ this.state.rate ? "green" : "white" }>£{rate}</p>, estimating that you work 44 weeks 
                per year, generating gross annual contracting fees of <p className={ this.state.totalFees ? "green" : "white" }>£{totalFees}</p>, while claiming expenses amounting to <p className={ this.state.expenses ? "green" : "white" }>£{expenses}</p>.</p>
              </div>
              <div className="calc-div" id="right">
                <h2>Comparison</h2>
                <table>
                    <tr>
                        <th class="col1"></th>
                        <th class="col23"><p>Inside IR35</p></th>
                        <th class="col23"><p>Outside IR35</p></th>
                    </tr>
                    <tr>
                        <td class="col1"><p>Total tax liability</p></td>
                        <td className={ this.state.combinedFixed <= this.state.combinedOutsideFixed ? "greenBox" : "redBox" }><p className="white">£{combinedFixed}</p></td>
                        <td className={ this.state.combinedOutsideFixed <= this.state.combinedFixed ? "greenBox" : "redBox" }><p className="white">£{combinedOutsideFixed}</p></td>
                    </tr>
                    <tr>
                        <td class="col1"><p>Annual post-tax income</p></td>
                        <td className={ this.state.insideAnnualFixed >= this.state.outsideAnnualFixed ? "greenBox" : "redBox" }><p className="white">£{insideAnnualFixed}</p></td>
                        <td className={ this.state.outsideAnnualFixed >= this.state.insideAnnualFixed ? "greenBox" : "redBox" }><p className="white">£{outsideAnnualFixed}</p></td>
                    </tr>
                    <tr>
                        <td class="col1"><p>Monthly post-tax income</p></td>
                        <td className={ this.state.insideMonthlyFixed >= this.state.outsideMonthlyFixed ? "greenBox" : "redBox" }><p className="white">£{insideMonthlyFixed}</p></td>
                        <td className={ this.state.outsideMonthlyFixed >= this.state.insideMonthlyFixed ? "greenBox" : "redBox" }><p className="white">£{outsideMonthlyFixed}</p></td>
                    </tr>
                </table>
              </div>
              <div className="calc-div" id="left">
                <h2>Inside IR35</h2>
                <ul>
                    <li>Your gross earnings are <p className={ this.state.totalFees ? "green" : "white" }>£{totalFees}</p>.</li>
                    <li>Your income tax liability is <p className={ this.state.incomeTaxFixed ? "red" : "white" }>£{incomeTaxFixed}</p>.</li>
                    <li>Your National Insurance liability is <p className={ this.state.nationalInsuranceFixed ? "red" : "white" }>£{nationalInsuranceFixed}</p>.</li>
                    <li>Your overall combined tax liability is <p className={ this.state.combinedFixed ? "red" : "white" }>£{combinedFixed}</p>.</li>
                    <li>Working 'inside IR35', your annual post-tax income amounts to <p className={ this.state.insideAnnualFixed ? "green" : "white" }>£{insideAnnualFixed}</p>.</li>
                    <li>This amounts to <p className={ this.state.insideMonthlyFixed ? "green" : "white" }>£{insideMonthlyFixed}</p> per month.</li>
                </ul>
              </div>
              <div className="calc-div" id="right">
                <h2>Outside IR35</h2>
                <ul>
                    <li>After expenses of <p className={ this.state.expenses ? "green" : "white" }>£{expenses}</p>, your gross earnings are <p className={ this.state.rate ? "green" : "white" }>£{earnings}</p>.</li>
                    <li>Your Corporation Tax liability is <p className={ this.state.corporationTaxFixed ? "red" : "white" }>£{corporationTaxFixed}</p>.</li>
                    <li>The income tax due on your dividends is <p className={ this.state.incomeTaxOutsideFixed ? "red" : "white" }>£{incomeTaxOutsideFixed}</p>.</li>
                    <li>Your overall combined tax liability is <p className={ this.state.combinedOutsideFixed ? "red" : "white" }>£{combinedOutsideFixed}</p>.</li>
                    <li>Working 'outside IR35', your annual post-tax income amounts to <p className={ this.state.outsideAnnualFixed ? "green" : "white" }>£{outsideAnnualFixed}</p>.</li>
                    <li>This amounts to <p className={ this.state.outsideMonthlyFixed ? "green" : "white" }>£{outsideMonthlyFixed}</p> per month.</li>
                </ul>
              </div>
              <p>Please note: These calculations are accurate as of the 2019-20 tax year.</p>
            </div>
        );
    }
}

export default Calculator;