import React, { Component } from 'react';
import './App.css';
import Banner from './components/Banner';
import Intro from './components/Intro';
import Calculator from './components/Calculator';

class App extends Component {
  state = {
    visible: true
  }
  render () {
    const buttonText = this.state.visible ? "Calculate Tax" : "Hide Calculation";
    return (
      <div className="App">
        <Banner />
        <Intro />
        <button onClick={() => {
          this.setState({ visible: !this.state.visible });
        }}>{buttonText}</button>
        { this.state.visible ? null : <Calculator /> }
      </div>
    );
  }
}

export default App;