import React, { Component } from 'react';
import './Intro.css';

class Intro extends Component {
    render() {
        return (
            <div className="intro">
                <p>The Off-Payroll working rules are due to be implemented across the private sector from April 2020, introducing significant 
                    changes to how the IR35 tax legislation is applied.</p>
                <p>Notably, the Off-Payroll rules hold end-clients responsible for assessing and determining the IR35 status of their contractors, 
                    while imposing an employment tax liability on parties engaging directly with contractors deemed 'inside IR35'.</p>
                <p>Contractors considered caught by the Off-Payroll rules are also unable to claim tax relief on business expenses, with the 5% 
                    allowance under the IR35 legislation having been withdrawn.</p>
                <p>If you're a contractor, use this Off-Payroll tax calculator to better understand the financial impact of the Off-Payroll rules 
                    on your earnings and tax liability.</p>
            </div>
        )
    }
}

export default Intro;