import React, { Component } from 'react';
import './Banner.css';
import banner from '../media/off-payroll.png';

class Banner extends Component {
    render() {
        return (
            <div className="header">
                <img src={banner} alt="The Off-Payroll rules" className="banner"></img>
                <div className="banner-text">
                    <h1>OFF-PAYROLL TAX CALCULATOR</h1>
                </div>
            </div>
        )
    }
}

export default Banner;